<template>
    <div class="ft-head-container">
        <div class="ft-head-menus">
            <ul v-for="menuItem in menus" @mouseover="showItem(menuItem.code)" @mouseout="hideItem">
                <span>{{menuItem.name}}</span>
                <div class="ft-head-menus-item">
                    <li v-for="menu in menuItem.items" v-show="selectItem==menuItem.code?true:false">
                        <span  @click="gotoHttpTest(menuItem.code,menu)" class="animated flipInY">{{menu.name}}</span>
                    </li>
                </div>
                
            </ul>
        </div>
        <div class="ft-head-icon">
            <Suggest></Suggest>
        </div>
    </div>
</template>
<script>
import Suggest from '@/components/head/Suggest';
import {mapState,mapMutations} from 'vuex';

export default {
    data(){
        return{
            selectItem:''
        }
    },
    methods:{
        ...mapMutations(['getMenus']),
        showItem:function(code){
            this.selectItem=code;
        },
        hideItem:function(){
            this.selectItem='';
        },
        gotoHttpTest:function(pCode,item){
            this.getMenus(pCode,item.code);
            this.$router.push({path:item.url});
            this.hideItem();
        }
    },
    computed:{
        ...mapState(['menu','menus'])
    },
    components:{
        Suggest:Suggest
    }    
}
</script>
<style lang="less" scoped>
@head-height:60px;
@span-width:120px;
@head-bg-color:rgba(0,0,0,0.8);
@head-hover-bg-color:rgba(0,0,0,0.2);
@font-color:#ffb44a;
@font-hover-color:#ff9500;
@font-size:16px;
@font-weight:400;
@a-hundred-percent:100%;

.ft-head-container{
    width: @a-hundred-percent;
    height: @head-height;
    background:@head-bg-color;
    .ft-head-menus{
        width: calc(@a-hundred-percent -  @head-height*2);
        height: @head-height;
        float: left;
        ul{
            position: relative;
            width: @span-width;
            height: @a-hundred-percent;
            color: @font-color;
            float: left;
            span{
                display: block;
                width: @span-width;
                height: @head-height;
                text-align: center;
                line-height: @head-height;
                font-size: @font-size;
                font-weight:@font-weight*2;
            }
            &:hover span{
                color: @font-hover-color;
                cursor: pointer;
                font-weight:@font-weight*2;
                background: @head-hover-bg-color;
            }
            .ft-head-menus-item{
                position: absolute;
                left: 0;
                top: 60px;
                background: @head-bg-color;
                li{
                    list-style: none;
                    span{
                        display: block;
                        width: @span-width;
                        height: @head-height;
                        text-align: center;
                        line-height: @head-height;
                        font-size: @font-size;
                        font-weight:@font-weight*2;
                        color:@font-color;
                    }
                    &:hover span{
                        color: @font-hover-color;
                        cursor: pointer;
                        font-weight:@font-weight*2;
                        background: @head-bg-color;
                    }
                }
            }
        }
    }
    .ft-head-icon{
        width: @head-height*2;
        height: @head-height;
        float: left;
    }
    
}
</style>



