import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const state={
    menu:{},
    menus:[{
        id:'1',
        name:'Http工具',
        code:'http',
        items:[{
            id:'101',
            name:'http模拟测试',
            code:'http1',
            icon:'',
            url:'/'
        }]
    }
    ,{
        id:'2',
        name:'格式化工具',
        code:'format',
        items:[{
            id:'201',
            name:'json格式化',
            code:'json1',
            icon:'',
            url:'json'
        },{
            id:'202',
            name:'xml格式化',
            code:'xml',
            icon:'',
            url:'xml'
        }]
    }]

}

const mutations={
    getMenus(state,pCode,code){
        for(var i=0;i<state.menus.length;i++){
            var childMenus=state.menus[i];
            if(childMenus.code==pCode){
                for(var j=0;j<childMenus.items.length;j++){
                    var item=childMenus.items[j];
                    if(item.code==code){
                        state.menu=item;
                        break;
                    }
                }
            }
        }
    }
}

export default new Vuex.Store({
    state,
    mutations
})


